<template>
  <div class="content">
        <h2>快速上手</h2>
        <p>Osff Design 致力于提供给程序员愉悦的开发体验。</p>
        <h3 style="margin:50px 0 20px">第一步：获取Osff Design</h3>
        <a-card style="margin-top: 20px" title="源码下载">
            <a-list item-layout="horizontal" bordered :data-source="version">
                <a-list-item slot="renderItem" slot-scope="item, index">
                    <a slot="actions" :href="item.file" >下载</a>
                    <a-list-item-meta :description="item.desc">
                        <h3 slot="title">{{ item.title }}</h3>
                    </a-list-item-meta>
                </a-list-item>
            </a-list>
        </a-card>

        <h3 style="margin:50px 0 20px">第二步：创建项目根目录</h3>
        <p>将下载的 osff-design 文件夹放入项目根目录</p>
        <div class="codeView" v-highlight>
            <pre><code v-html="folder"></code></pre>
        </div>

        <h3 style="margin:50px 0 20px">第三部步：页面引入</h3>

        <div class="action">
            <!-- <a-tag 
            color="cyan"
            style="cursor: pointer;"
            v-clipboard:copy="template.copy" 
            v-clipboard:success="copySuccess" 
            v-clipboard:error="onError">复制代码</a-tag> -->
            <span></span>
            <span style="color:#999">HTML</span>
        </div>
        <div class="codeView" v-highlight>
            <pre><code v-html="template.code"></code></pre>
        </div>

        <h3 style="margin:50px 0 20px">导入完成</h3>
        <p>如此简单，现在就去体验一下吧</p>
       
  </div>
</template>

<script>

import utils from '@/assets/js/utils.js'
const version = [
  {
    title: '1.0.0',
    desc: '开发版本',
    file:'https://github.com/bestlin95/osff-design/archive/master.zip'
  }
];

const folder = `
/*
    your-project------------------------------------------------你的项目根目录
        osff-design---------------------------------------------Osff Design源码目录
*/

`;

const code = utils.htmlEncode(`
    <link rel="stylesheet" href="./osff-design/styles/index.css">

    &lt;script src="./osff-design/require.js" defer async="true" data-main="./osff-design/main.js"&gt;&lt;/script&gt;

`
);

const copy = ``;



export default {
    data(){
        return{
            version,
            folder,
            template:{
                code:code,
                copy:copy
            }
        }
    },
    created(){
    },
    methods:{
        copySuccess(){
            this.$message.success('复制成功');
        },
        onError(){
            this.$message.error('复制失败');
        }        
    }
}
</script>
